import React from 'react';

interface ItemProps {
	itemData: {
		titleDescription: string;
		description: string;
		createdAt: string;
		id: number;
		isCompleted: boolean;
		updatedAt: string;
		priority: string;
	}
	itemDone: (id:number)=>void;
}

const TaskItem = (props: ItemProps) => {
	const { itemData, itemDone } = props;
	const classStyle = `task-item ${itemData.isCompleted? 'done' : ''} ${itemData.priority}`

	return (
		<div className={classStyle} onClick={() => itemDone(itemData.id)}>
	    <p className="task-text">{itemData.titleDescription}</p>
	    <p>{itemData.description}</p>
	    <p>Last modified on: {itemData.createdAt}</p>
	    <p>Created at: {itemData.updatedAt}</p>
	  </div>
	)
}

export default TaskItem;