import React from 'react';
import { connect } from 'react-redux';

interface Props {
  showModalForm: () => void;
}

function CategoryAddButton(props: Props) {
  const { showModalForm } = props;
  const hdlClackAdd = () => {
    showModalForm();
  };

  return (
    <div className="cg-button">
      <button onClick={hdlClackAdd} type="button">
        Add todos
      </button>
    </div>
  );
}

interface Dispatch {
  (obj: { type: string }): void;
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
  showModalForm: () => dispatch({ type: 'SHOW_MODAL' }),
});

export default connect(null, mapDispatchToProps)(CategoryAddButton);
