import React, { useState } from 'react';
import { connect } from 'react-redux';
import { StateInit } from '../../store/reducer';
import CategoryAddButton from '../../components/CategoryAddButton/CategoryAddButton';
import CategoryList from '../../components/CategoryList/CategoryList';
import SlideButton from '../SlideButton/SlideButton';
// import { DragDropContext } from 'react-beautiful-dnd';
// import cn from 'classnames';

interface Props {
  state: StateInit;
  selectCategory: (id: number) => void;
}


function TodoCategories(props: Props) {
  const { state, selectCategory } = props;
  const [isShow, setVisibility] = useState(false);
  const curCatId = state.currentCategoryId;
 
  const categoryClick = (id: number) => {
    selectCategory(id);
  };
  const showClick = () => {
  	setVisibility(!isShow);
  	console.log('show click...');
  }
  

  const keyDown = () => {};
  return (
    <>
      <div className="cg-title">
        <h3> Todo categories </h3>
      </div>
      <div 
      	className={`back-font ${(isShow)? 'fadein': 'fadeout'}`}
      	onClick={showClick}>
      </div>
      <SlideButton toggleClick={showClick} rotateArrow={isShow}/>
      			<div className={`cg-container ${(isShow)? 'show': 'hide'}`}>
	      			{curCatId && <CategoryList
	      				curCatId={curCatId} 
	      				itemClick={categoryClick}
	      				categoryList={state.categoryes}/>
	      			}	
	        		<CategoryAddButton />
	     			</div>
    </>
  );
}
interface Dispatch {
  (type: { type: string; payload: number }): void;
}

const mapStateToProps = (state: StateInit) => ({ state });
const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    selectCategory: (id: number) =>
      dispatch({ type: 'SELECT_CATEGORY', payload: id }),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TodoCategories);
