import React, {useState} from 'react';
import { TasksList } from '../../store/reducer';
import  TaskItem from '../TaskItem/TaskItem';
import ModalForm, {Values, ErrorObject} from '../ModalForm/ModalForm';
import {connect} from 'react-redux';

interface Props {
  tasksList: Array<TasksList>;
  categoryTitle: string;
  addTaskItem: (values: Values) => void;
  taskIsDone: (id: number) => void;
}

function TasksTable(props: Props) {
  const { 
  	tasksList, 
  	categoryTitle, 
  	addTaskItem, 
  	taskIsDone, 
  } = props;
  const [isVisible, setVisibility] = useState(false);
  const visible = (categoryTitle === '')? false : true;
  const inputBox = {
    name:'task',
    label:'Input task title',
    placeholder:'for example: To do something...',
    maxlen:20,
  };
  const memoBox = {
  	label:'Task description',
    name:'memo',
    placeholder:'task ddescription...', 
    maxlen:100,
  }
  const radioBox = [
  	{name:'normal',defaultChecked: true},
    {name:'warning',defaultChecked: false},
    {name:'danger',defaultChecked: false},
  ];

  const addTask = () => {
  	setVisibility(true);
  }
  const okClick = (values: Values) => {
  	if(values.inputValue.length > 0)
  	addTaskItem(values);
  	console.log(values);
  	setVisibility(false);
  }
  const cancelClick = () =>{
  	setVisibility(false);
  }
  const itemDone = (id: number) => {
  	taskIsDone(id);
  }

  return (
  	<>
	  	<div className="task-title">{categoryTitle}</div>
	    <div className="task-list">
	      {
	      	tasksList.map((itemData, idx) => (
	      		<TaskItem 
	      			key={itemData.id + 'task-item'}
	      			itemDone={itemDone}
	      			itemData={itemData}/>))
	      }
	      {visible && <div className="task-add-button" onClick={addTask}>+</div>}
	      <ModalForm
	        inputData={{
	          formTitle: 'Create new task',
	          visible: isVisible,
	          inputBox: inputBox,
	          onOK: okClick,
	          onCancel: cancelClick,
	          memoBox: memoBox,
	          radioBox: radioBox,
        	}} 
	      >{(error: ErrorObject) => (
	      	error && 
	      	<p>
	      		{error.input && error.input.empty && 'Need to input task title!'}
	      		{error.input && error.input.maxlen && 'Must be less then 20 chars!'}
	      		{error.memo && error.memo.empty && 'Need to input task description!'}
	      		{error.memo && error.memo.maxlen && 'Must be less then 100 chars!'}
	      		{error.radio && error.radio.empty && 'Need to select a radio item!'}
	      	</p>)
	    	 }
	      </ModalForm>
	    </div>
    </>
  );
}

interface Dispatch{
	(type:{
		type: string,
		payload: Values | number,
	}): void;
}

const mapDispatchToProps = (dispatch:Dispatch) => ({
	addTaskItem: (values: Values) => dispatch({type:'ADD_TASK_ITEM', payload: values}),
	taskIsDone: (id: number) => dispatch({type:'TASK_DONE_TOGGLE', payload: id})
})

export default connect(null,mapDispatchToProps)(TasksTable);
