import { Values } from '../components/ModalForm/ModalForm';
import {todoList} from './todoList'

interface Actions {
  type: string;
  payload?: Values | number | string 
   | [number, number | null]
   | [string, number | null];
}


const initState = {
  currentCategoryId: 1,
  currentTaskId: 1,
  isVisibleModal: false,
  lastCatId:1,
  lastTaskId:8,
  categoryes: [
    {
      id: 1,
      description: 'Category 1',
      isCompleted:false,
      createdAt: new Date().toLocaleString('en-US'),
      tasksList: todoList,
    },
  ],
};
export interface TasksList {
  id: number;
  titleDescription: string;
  description: string;
  isCompleted: boolean;
  createdAt: string;
  updatedAt: string;
  priority: string;
}

export interface Categoryes {
  id: number;
  description: string;
  createdAt: string;
  isCompleted: boolean;
  tasksList: Array<TasksList>;
}

export interface StateInit {
  currentCategoryId: number | null;
  currentTaskId: number;
  isVisibleModal: boolean;
  lastCatId: number;
  lastTaskId: number;
  categoryes: Array<Categoryes>;
}

class TaskItem {
  static _lastId: number | null = null;
  public id: number;
  public titleDescription: string;
  public description: string;
  public createdAt: string;
  public updatedAt: string;
  public priority: string;
  public isCompleted: boolean;
  constructor(values:Values, lastId: number = 1){
    if(TaskItem._lastId === null) TaskItem._lastId = lastId;
    this.id = this._createNewID();
    this.titleDescription = values.inputValue;
    this.description = values.memoValue;
    this.createdAt = new Date().toLocaleString('en-US');
    this.updatedAt = new Date().toLocaleString('en-US');
    this.priority = this._convertToString(values.radioBox);
    this.isCompleted = false;
  }
  _createNewID(): number{
   if(TaskItem._lastId !== null) TaskItem._lastId += 1;
   else TaskItem._lastId = 1;
   return TaskItem._lastId;
  }
  _convertToString(p: Array<boolean>): string{
    const priority = ['normal','warning','danger'];
    return priority[p.findIndex(i=>i)];
  }
}

class CategoryItem{
  static _lastId:number | null = null;
  
  public id: number;
  public description: string;
  public isCompleted: boolean;
  public createdAt: string;
  public tasksList: []; 
  constructor(title: string, lastId: number = 1){
    if(CategoryItem._lastId === null){
      CategoryItem._lastId = lastId;
    }
    this.id = this.createNewID();
    this.description = title;
    this.isCompleted = false;
    this.createdAt = this.getDate();
    this.tasksList = [];
  }
  getDate(){
    return new Date().toLocaleString('en-US');
  }
  createNewID(){
    if(CategoryItem._lastId !== null)  CategoryItem._lastId += 1;
    else CategoryItem._lastId = 1;
    return CategoryItem._lastId;
  }
}

